
import { Entry } from "@/models/Chapter";
import { Options, Vue } from "vue-class-component";

@Options({
  name: "Markdown",
  props: {
    entry: Object,
  },
})
export default class Markdown extends Vue {
  private entry!: Entry;
  private chapter = "";

  mounted(): void {
    this.chapter = this.entry.parameters[0];
  }
}
